import { mapState,mapMutations} from 'vuex'
import PubClassify from '@/components/publish/pubClassify.vue'
import RelationType from '@/components/publish/relationType.vue'
export default{
	data(){
		return{
			imageList:[],
			title:'',
			text:''
		}
	},
	computed:{
		message:state=>state.message
	},
	components:{
		PubClassify,
		RelationType
	},
	methods:{
		...mapMutations([
			'getMessage'
		]),
		getPicture(){
			let that=this;
			uni.chooseImage({
				count:1,
				sizeType:'compressed',
				success:(res)=>{
					that.imageList=res.tempFilePaths
				}
			})
		},
		getPreview(index){
			let that=this;
			uni.previewImage({
				current:index,
				urls:that.imageList
			})
		},
		deleteImg(index){
			this.imageList.splice(index,1)
		},
		lostSubmit(){//发布
		console.log(this.text+'---'+this.title+'---'+this.imageList)
		console.log(this.message)
			let that=this;
			if(!that.sessionKey){
				uni.showToast({
					title: '请先完成授权!',
					icon:'none'
				});
				return false;
			}
		}
	}
}
